.thank {
    .container {
        display: flex;
        align-items: center;
        justify-content: left;
        height: calc(100vh - 91px);
    }

    h1 {
        margin-bottom: 10px;
        font-weight: 700;
        font-size: 72px;
        line-height: 80px;
        letter-spacing: -0.02em;
    }

    p {
        margin-bottom: 34px;
        font-size: 16px;
        line-height: 24px;
        letter-spacing: -0.01em;
        color: $grey-text;
    }

    &__box {
        max-width: 754px;
    }

    img {
        position: absolute;
        right: 0;
        bottom: 0;
        z-index: -1;
        max-width: 860px;
        max-height: 854px;
        object-fit: cover;
    }
}