html,
body {
    height: 100%;
}

body {
    position: relative;
    display: flex;
    flex-direction: column;
    counter-reset: section;
    font-family: "Manrope", sans-serif;
    font-weight: 400;
    font-size: 18px;
    line-height: 32px;
    color: $black;
    background-color: $grey-white;
}

strong {
    font-weight: 700;
}

.main {
    flex-grow: 1;
    background-color: $grey-white;
}

.container {
    margin: 0 auto;
    padding: 0 24px;
    max-width: 1248px;

    &--technical {
        padding: 0 36px;
        max-width: 1920px;
    }

    &--main {
        padding: 0 24px;
        max-width: 1448px;
    }
}

.round {
    z-index: 3;
    margin: -24px 0;
    border-radius: 24px;
}

.round-thank {
    z-index: 1;
    margin: -24px 0 0;
    border-radius: 24px 24px 0 0;
}

.button-home {
    display: block;
    padding: 16px;
    max-width: 205px;
    border: 2px solid $black;
    border-radius: 25px;
    font-weight: 700;
    font-size: 14px;
    line-height: 20px;
    text-align: center;
    cursor: pointer;
}

blockquote {
    margin-top: 20px;
    margin-bottom: 20px;
    padding-right: 20px;
    padding-left: 20px;
    border-left: 2px solid  #2fba56;
    font-weight: 700;
    font-size: 32px;
}

.date {
    display: flex;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: -0.01em;

    &__calendar {
        position: relative;
        padding-right: 44px;

        &::after {
            content: "";
            position: absolute;
            right: 11px;
            top: 50%;
            width: 20px;
            height: 1px;
            background-color: $black;
            transform: translateY(-50%);
        }
    }
}

.general__link {
    display: flex;
    align-items: center;
    max-width: fit-content;
    background-color: transparent;

    span {
        display: block;
        margin-right: 12px;
        font-size: 20px;
        letter-spacing: -0.01em;
    }
}

.integration {
    margin-bottom: 152px;

    &__text {
        margin-bottom: 50px;
        max-width: 620px;
        font-size: 16px;
        line-height: 24px;
        letter-spacing: -0.01em;
        color: $grey-text;
    }

    ul {
        display: flex;
        justify-content: space-between;

        li {
            position: relative;
            padding: 32px 36px;
            width: 100%;
            min-height: 560px;
            overflow: hidden;
            border-radius: 24px;
            background-color: $white;

            h3 {
                margin-bottom: 14px;
                max-width: 286px;
                font-weight: 700;
                font-size: 24px;
                letter-spacing: -0.02em;
            }

            p {
                max-width: 538px;
                font-size: 16px;
                line-height: 24px;
                letter-spacing: -0.01em;
                color: $grey-text;
            }

            img {
                position: absolute;
                right: 0;
                bottom: 0;
                max-width: 500px;
                object-fit: cover;
            }

            &:first-child {
                margin-right: 24px;
                max-width: 70%;
            }

            &:last-child {
                max-width: 30%;
            }
        }
    }
}

.category {
    display: inline-block;
    margin: auto 0 10px;
    padding: 0 12px;
    width: fit-content;
    height: 32px;
    border-radius: 6px;
    font-weight: 600;
    font-size: 14px;
    letter-spacing: -0.01em;
    text-align: center;
    color: $white;
    background-color: $green;
}

.business {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 232px;

    &__wrapper {
        max-width: 50%;
    }

    h4 {
        margin-bottom: 22px;
        max-width: 450px;
        font-weight: 700;
        font-size: 32px;
        line-height: 40px;
        letter-spacing: -0.02em;
    }

    p {
        margin-bottom: 24px;
        max-width: 500px;
        font-size: 16px;
        line-height: 24px;
        letter-spacing: -0.01em;
        color: $grey-text;
    }

    img {
        max-width: 50%;
        object-fit: cover;
    }
}

.sales {
    position: relative;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    margin-bottom: 152px;
    max-height: 800px;
    overflow: hidden;
    border-radius: 24px;
    color: $white;
    background-color: $black;

    &__wrapper {
        margin: 64px 0 109px 64px;
        max-width: 50%;
    }

    h3 {
        max-width: 406px;
    }

    p {
        margin-bottom: 50px;
        max-width: 405px;
        font-size: 16px;
        line-height: 24px;
        letter-spacing: -0.01em;
        color: $grey-text;
    }

    img {
        margin-top: 45px;
        max-width: 50%;
        object-fit: contain;
    }

    ul {
        position: relative;

        &::before {
            content: "";
            position: absolute;
            left: 0;
            top: 0;
            width: 2px;
            height: 100%;
            min-height: 286px;
            border-radius: 8px;
            background-color: rgba($white, 0.15);
        }
    }

    &__item {
        position: relative;
        margin-bottom: 32px;
        padding-left: 64px;
        transition: margin-bottom 0.3s;
        cursor: help;

        &::before {
            content: "";
            position: absolute;
            left: 0;
            width: 2px;
            height: 70px;
            border-radius: 8px;
            background-color: transparent;
            transform: translateY(-30px);
            transition: background-color 0.3s, transform 0.3s;
        }

        &::after {
            content: "0" counter(section) "";
            position: absolute;
            left: 34px;
            top: 15px;
            opacity: 0.15;
            width: 16px;
            height: 16px;
            counter-increment: section;
            font-size: 12px;
            line-height: 16px;
            text-align: center;
            color: $white;
            transition: opacity 0.3s;
        }

        h4 {
            z-index: 500;
            opacity: 0.15;
            font-weight: 700;
            font-size: 32px;
            line-height: 40px;
            letter-spacing: -0.02em;
            background-color: $black;
            transition: opacity 0.3s;
        }

        a {
            position: absolute;
            left: 64px;
            top: 46px;
            opacity: 0;
            padding-right: 27px;
            font-size: 16px;
            line-height: 24px;
            letter-spacing: -0.01em;
            transition: opacity 0.3s, top 0.3s;
            cursor: pointer;

            &::after {
                content: "";
                position: absolute;
                right: 0;
                top: 50%;
                width: 14px;
                height: 11px;
                background-image: url("../images/svg/arrow-right.svg");
                transform: translateY(-50%);
            }
        }
    }

    &__item--active {
        margin-bottom: 62px;
        cursor: default;

        &::before {
            background-color: $green;
            transform: translateY(0);
        }

        &::after {
            opacity: 1;
        }

        a {
            opacity: 1;
        }

        h4 {
            opacity: 1;
        }
    }
}

.retail {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 152px;
    padding: 64px 0 50px 64px;
    overflow: hidden;
    border-radius: 24px;
    background-color: $white;

    &__wrapper {
        max-width: 50%;
    }

    img {
        max-width: 50%;
        object-fit: cover;
    }

    h3 {
        max-width: 456px;
    }

    .retail__text {
        margin-bottom: 53px;
        max-width: 557px;
        font-size: 16px;
        line-height: 24px;
        letter-spacing: -0.01em;
        color: $grey-text;
    }

    ul {
        li {
            padding-left: 60px;
            max-width: 480px;
            background-position: top left;
            background-size: 40px;
            background-repeat: no-repeat;

            h4 {
                margin-bottom: 7px;
                font-weight: 700;
                line-height: 28px;
                letter-spacing: -0.01em;
            }

            p {
                font-size: 16px;
                line-height: 24px;
                letter-spacing: -0.01em;
                color: $grey-text;
            }

            &:not(:last-child) {
                margin-bottom: 37px;
            }
        }
    }
}

.more {
    display: flex;
    justify-content: space-between;
    margin: 0 36px 36px;

    &__item {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        min-height: 472px;
        border-radius: 24px;
        background-color: $white;

        h2 {
            margin-bottom: 23px;
            max-width: 376px;
        }

        .general__link {
            span {
                font-size: 16px;
                line-height: 24px;
            }
        }

        &:first-child {
            margin-right: 24px;
        }
    }
}
